.ContentCard{
    .card{
      margin: 30px;
      padding: 10px;
      display: flex;
      justify-content: space-around;
      align-items: center;
      width: 300px;
      height: 400px;
      .AvatarContainer{
        display: flex;
        justify-content: center;
        .avatar{
          display: flex;
          width: 200px;
          height: 200px;
          border: darkgray solid 3px;
        }
      }
      .content{
        .socialRow{
          height: 20px;
          display: flex;
          justify-content: space-around;
          .socialIcon{
            color: darkgray;
            height: 30px;
          }
        }
      }
    }
  }